.Lable {
    padding-top: 35px;
    font-size: 25px;
    height: 35px;
    width: 600px;
}

.WinnersBlock {
    width: 600px;
}

.table {
    display: inline-block;
    width: 600px;
    display: grid;
    grid-template-areas: 
    "row1"
    "row2"
    "row3"
    "row4";
}
.row1 {
    background-color: #C3C3C3;
    width: 100%;
    display: grid;
    grid-template-areas: 
    "col1 col2 col3";  
    grid-template-columns: 20% 60% 20%;
}
.row2 {
    background-color: #FFF;
    width: 100%;
    display: grid;
    grid-template-areas: 
    "colum1 colum2 colum3";  
    grid-template-columns: 20% 60% 20%;
}
.row3 {
    background-color: #FFF;
    width: 100%;
    display: grid;
    grid-template-areas: 
    "colum1 colum2 colum3"; 
    grid-template-columns: 20% 60% 20%; 
}
.row4 {
    background-color: #FFF;
    width: 100%;
    display: grid;
    grid-template-areas: 
    "colum1 colum2 colum3"; 
    grid-template-columns: 20% 60% 20%; 
}

.colum1 {
    text-align: center;
    height: 40px;
    border: 1px solid #D4D2D5;
}
.colum2 {
    text-align: center;
    border: 1px solid #D4D2D5;
}
.colum3 {
    text-align: center;
    border: 1px solid #D4D2D5;
}


.col1 {
    text-align: center;
    height: 40px;
    box-shadow: inset -2px -2px 0px #262626, inset 2px 2px 0px #F0F0F0, inset -4px -4px 0px #7E7E7E;
}
.col2 {
    text-align: center;
    box-shadow: inset -2px -2px 0px #262626, inset 2px 2px 0px #F0F0F0, inset -4px -4px 0px #7E7E7E;
}
.col3 {
    text-align: center;
    box-shadow: inset -2px -2px 0px #262626, inset 2px 2px 0px #F0F0F0, inset -4px -4px 0px #7E7E7E;
